<!-- 分类 -->
<template>
  <div class="BookClassify">
    <div class="BookClassify-box">
      <div class="top-type">
        <div class="top-select">
          <div class="top-tab" :class="[activeIndex === index ? 'active' : '']" @click.stop="getNodeId(tit.node[0].id, index)" v-for="(tit, index) in TopData" :key="index">
            {{ tit.name }}
          </div>
        </div>
        <div class="top-search" @click.stop="inputing">
          <van-icon name="search" />
        </div>
      </div>
      <!--  -->
      <div class="middle-type">
        <div class="middle-select">
          <div class="middle-tab" @click.stop="getLabelUrl(tab)" v-for="(tab, index) in tabData" :key="index">
            {{ tab.name }}
          </div>
        </div>
      </div>
      <!--  -->
      <div class="bottom-type">
        <div class="bottom-select">
          <div class="select-one">
            <div class="one-title" :class="[tt === index ? 'tabActive' : '']" v-for="(tit, index) in catArrayData" :key="index" @click.stop="getTit(tit, index)">{{ tit.name }}</div>
          </div>
          <div class="filtrate-tab" @click.stop="isShow = !isShow ? true : false">
            <div class="tab-img">
              <van-icon name="arrow-up" />
            </div>
            <div class="tab-title">筛选</div>
          </div>
        </div>
        <!--  -->
        <div class="bottom-hidebox" v-show="isShow">
          <div class="hide-box" v-if="categoriesData">
            <div class="hide-tab">
              <div class="item" :class="[tp === index ? 'tabActive' : '']" v-for="(item, index) in hideData0" :key="index"
              @click.stop="getPay(item, index)"
              >
                {{item.name}}
              </div>
              </div>
              <div class="hide-tab">
              <div class="item" :class="[tb === index ? 'tabActive' : '']" v-for="(item, index) in hideData1" :key="index"
              @click.stop="getBookStatus(item, index)"
              >
                {{item.name}}
              </div>
              </div>
              <div  class="hide-tab">
              <div class="item" :class="[tw === index ? 'tabActive' : '']" v-for="(item, index) in hideData2" :key="index"
               @click.stop="getWordCounts(item, index)"
              >
                {{item.name}}
              </div>
              </div>
          </div>
        </div>
      </div>
     </div>
      <!--  -->
         <div class="classify-list">
             <div class="book-item"
              v-for="(book,index) in listData" :key="index"
              @click="gitBookDetails(book)"
              >
                <div class="book-img">
                    <img :src="book.cover" alt="">
                </div>
                 <div class="book-title">
                   {{book.title}}
                 </div>
                 <div class="book-author">
                     {{book.author}}
                 </div>
             </div>
         </div>
         <!--  -->
         <div class="loadmore" ref='loadmore'>
         <van-loading type="spinner"
         size="20px" 
         color="#1989fa" />
          <div>
             正在努力赶来ing
         </div>
       </div>
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    //这里存放数据
    return {
      TopData: [],
      activeIndex: 0,
      tabData: [],
      catId: "",
      categoriesData: [],
      catArrayData: [],
      hideData0:[],
      hideData1:[],
      hideData2:[],
      sort: 1,
      wordCount: 0,
      bookStatus: 0,
      pay: 0,
      isShow: false,
      tt:0,
      tp: 0,
      tb:0,
      tw:0,
      next: "",
      listData: [],
      page:1,
      mrid:"",
      mrurl: "",
    };
  },
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    
  async  getNodeId(id, i) {
    this.activeIndex = i;
 await  this.axios.get("/store/info.json?type=female&bar=cat&uuid=" + id).then((re) => {
        this.tabData = re.list[0].labels;
        // console.log("this.tabData", this.tabData);
        this.mrurl=this.tabData[0];
        this.getLabelUrl(this.mrurl);
      });
    },
       async getTopData() {
      await this.axios.get("/store/node.json?gender=1").then((re) => {
        this.TopData = re.books;
        //   console.log("this.TopData",this.TopData);
         this.mrid = this.TopData[0].node[0].id;
          let i = 0;
        this.getNodeId(this.mrid, i);
      });
    },
    async getLabelUrl(e) {
      let a = e.url.split("?");
      this.catId = a[1]; //catId=""
    await  this.axios.get(e.url).then((re) => {
          this.categoriesData = re.categories.splice(0, 3);
         console.log("categoriesData", this.categoriesData);
         this.hideData0=this.categoriesData[0].catArray;
         this.hideData1=this.categoriesData[1].catArray;
         this.hideData2=this.categoriesData[2].catArray;
        //   console.log('this.hideData1',this.hideData1);
          this.catArrayData = re.categories[0].catArray;
          this.getResourceData();
      });
    },
  async  getResourceData() {
   await   this.axios.get("/source/v2/searchBook.json?" + this.catId + "&wordCount=" + this.wordCount + "&pay=" + this.pay + "&bookStatus=" + this.bookStatus + "&sort=" + this.sort+ "&page="+this.page).then((re) => {
        console.log("ResourceData=>", re);
        for(let i=0;i<re.list.length;i++){
        this.listData.push(re.list[i]);
        }
        console.log('this.listData',this.listData);
      });
    },
    gitBookDetails(e){
      console.log(e);
      if(this.activeIndex===3){
        this.$router.push({
            name:"ListenBook",
            params:{id:e.id}
        })
      }else{
          this.$router.push({
              name:'BookDetails',
              params:{id:e.id,title:e.title}
          })
      }
    },
    windowScroll(){
     let scrollTop=document.documentElement.scrollTop||document.body.scrollTop;
     let loadMoreoffsetTop=this.$refs.loadmore.offsetTop;
     let clientHeight=window.innerHeight;
    //  console.log(scrollTop,loadMoreoffsetTop,clientHeight);
     if(scrollTop+clientHeight>=loadMoreoffsetTop+33){
       this.page++;
       this.getResourceData();
     }
   },
    getTit(e, i){
     this.tt=i;
     this.sort = e.value;
    this.getResourceData();
    },
    getPay(e, i) {
      this.tp = i;
      this.pay = e.value;
    //   console.log('this.bookStatus',this.bookStatus);
      this.getResourceData();
    },
    getBookStatus(e, i){
      this.tb = i;
      this.bookStatus = e.value;
      console.log('this.bookStatus',this.bookStatus);
      this.getResourceData();
    },
    getWordCounts(e, i){
      this.tw = i;
      this.wordCount = e.value;
      this.getResourceData();
    },
    inputing(){
         this.$router.push({
      name:"SearchView",
    })
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.getTopData();
    this.getLabelUrl();
    this.getResourceData();
    this.getNodeId();
    this.getNextResourceData();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
      //挂载完成后监听窗口滚动事件
      window.addEventListener('scroll',this.windowScroll);
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {
      // 销毁之前移除监听
       window.removeEventListener('scroll',this.windowScroll);
  }, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
  //监听属性 类似于data概念
  components: {},
};
</script>
<style lang="scss" scoped>
.BookClassify {
  margin: 0 0 50px 0;
}
.BookClassify-box{
   position: sticky;
   top: 0;
   left: 0;
   background-color: #ffffff;
}
.active {
  font-weight: bolder;
  font-size: 16px;
}
.tabActive {
  color: #e4393c;
}
.top-type {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  margin: 0px 10px;
  .top-select {
    display: flex;
    .top-tab {
      font-size: 14px;
      padding-right: 15px;
    }
  }

  .top-search {
      font-size: 16px;
  }
}
.middle-select {
  display: flex;
  flex-wrap: wrap;
  background-color: #eeeeee;
  padding: 20px 0 10px 0;
  .middle-tab {
    font-size: 14px;
    padding: 0 0 10px 20px;
  }
}

.bottom-type {
  padding: 10px;
  margin: 0px 10px;
  .bottom-select {
    font-size: 12px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    .select-one {
      display: flex;
      align-items: center;
      .one-title:nth-child(1) {
        padding-right: 15px;
        border-right: 1px solid #999;
      }
      .one-title:nth-child(2) {
        padding-left: 15px;
      }
    }

    .filtrate-tab {
      display: flex;
      border: 0.02667rem solid #999;
      border-radius: 1.53333rem;
      padding: 3px 5px;
      .tab-img {
      }

      .tab-title {
      }
    }
  }
}

.bottom-hidebox {
  .hide-box { 
    .hide-tab {
     display: flex;
     flex-wrap: wrap;
       .item{
           padding: 10px 13px 10px 0;
       }
    }
  }
}
.classify-list {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    // margin-top: 4.5rem;
  .book-item {
       width: 100px;
       margin-bottom: 10px;
    .book-img {
      img {
         width: 100px;
        height: 135px;
      }
    }
    .book-title {
        font-size: 14px;
        padding: 5px 0;
    }

    .book-author {
      font-size: 12px;
      color: grey;
    }
  }
}
.loadmore{
    display: flex;
    justify-content: center;
   text-align: center;
   font-size: 14px;
   line-height: 40px;
 }
</style>
